.NavItem{
    margin: 10px 0px;
    width: 100%;
}
.NavItem a{
    color: #8f5c2c;
    display: block;
    width: 100%;
    text-align: center;
}
.NavItem a:hover, .NavItem a:active, .NavItem a.active{
    color: #40a4c8;
}
@media (min-width:500px){
    .NavItem{
        display: flex;
        align-items: center;
        width: auto;
        height: 100%;
    }
    .NavItem a{
        color: white;
        display: inline-block;
        width: auto;
        height: 100%;
        padding: 16px 10px;
        box-sizing: border-box;
        border-bottom: 4px solid transparent;
    }
    .NavItem a:hover, .NavItem a:active, .NavItem a.active{
        background-color: #8f5c2c;
        border-bottom: 4px solid #40a4c8;
        color: white;
    }
}