.Toolbar{
    height: 56px;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: #703b09;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0px 20px;
    box-sizing: border-box;
    z-index: 90;
    color: white;
}
.Toolbar nav{
    height: 100%;
}
.t{
    display: none;
}
@media (max-width:499px){
    .d{
        display: none;
    }
    .t{
        display: block;
    }
}