.Modal {
    position: fixed;
    z-index: 500;
    background-color: white;
    width: 70%;
    border: 1px solid #ccc;
    box-shadow: 1px 1px 1px black;
    padding: 16px;
    left: 50%;
    top: -100%;
    transform: translate(-50%,-50%) !important;
    box-sizing: border-box;
    transition: all 0.3s ease-out;
}
.Modal h1, .Modal p{
    text-align: center;
}
@media (min-width: 600px) {
    .Modal {
        width: 500px;
    }
}