.container{
    width: 100%;
    margin-bottom: 1rem;
    display: flex;
    align-items: center;
    justify-content: center;
}
.inner{
    width: 80%;
    padding: 1rem;
    box-shadow: 2px 1px 8px rgba(0,0,0,0.3);
    text-align: center;
}
.inner p{
    margin-bottom: 0.5rem;
    font-weight: 900;
}
.inner p span{
    font-weight: 400;
    display: inline-block;
    padding: 5px 10px;
    border: 1px solid rgba(0,0,0,0.1);
    margin-right: 10px;
}