.SideDrawer{
    position: fixed;
    width: 280px;
    max-width: 70%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 200;
    background-color: white;
    padding: 32px 16px;
    box-sizing: border-box;
    transition: transform 0.3s ease-out ;
    transform: translateX(-100%);
}
@media (min-width:500px){
    .SideDrawer{
        display: none;
    }
}
.Open{
    transform: translateX(0);
}
.Close{
    transform: translateX(-100%);
}
.close{
    width: 100%;
    text-align: right;
    padding-right: 5px;
    box-sizing: border-box;
    color: salmon;
    margin-bottom: 1rem;
}