.container{
  width: 100%;
  padding: 2rem 0rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.inner{
  width: 600px;
  padding: 1rem;
  box-shadow: 2px 1px 5px #ccc;
  text-align: center;
  border: 1px solid #703b09;
}
.inner h1{
  margin-bottom: 1rem;
}
.formCon{
  padding: 1rem;
}
.formCon form{
  width: 100%;
}
.formCon form input{
  width: 100%;
  box-sizing: border-box;
  padding: 0rem 1rem;
  height: 45px;
  margin-bottom: 1rem;
  border: 1px solid #703b09;
}
.formCon form label{
  display: block;
  font-size: 12px;
  text-align: left;
  margin-bottom: 0.5rem;
}
.modalBtn{
  display: flex;
  justify-content: center;
  align-items: center;
}